.sales__pitch{
    padding: 100px 0;
    background-image: url('../images/pattern_opacity_06.png');
    background-size: cover;
    background-position: center center;
    &-content{
        h3{
            font-family: $primary-font;
            color: $text-color-dark;
            font-size: 36px;
            font-weight: 600;
            line-height: 45px;
            margin-bottom: 30px;
        }
        p{
            color: $text-color;
            font-size: 18px;
            font-family: $secondary-font;
            line-height: 30px;
        }
    }
    &-image{
        position: relative;
        height: 600px;
        .illustration{
            position: absolute;
            font-family: $primary-font;
            &.middle{
                top:50%; left: 50%;
                transform: translate(-50%, -50%);
            }
            &.top-left{
                z-index: 1;
                top: 0px; left: -30px;
            }
            &.top-right{
                top: 140px; right: -40px;
                z-index: 1;
            }
            &.bottom-left{
                bottom: 60px; left: -30px;
                z-index: 1;
            }
        }
    }
}

.customer__reviews{
    padding: 100px 0;
    background-color: #F9F9F9;
    position: relative;
    &-separator{
        position: absolute;
        width: 100%;
        top: -95px;
        left: 0;
        background: url('../images/sep-grey.svg') repeat-x;
        background-size: cover;
        background-position: center top;
        height: 100px;
    }
    &-heading{
        h3{
            font-family: $primary-font;
            color: $text-color-dark;
            font-size: 36px;
            font-weight: 600;
            line-height: 45px;
            margin-bottom: 30px;
        }
        p{
            color: $text-color;
            font-size: 18px;
            font-family: $secondary-font;
            line-height: 30px;
            width: 70%;
            margin: 0 auto 80px;
        }
    }
    &-item{
        font-family: $secondary-font;
        background-color: $white;
        border-radius: 8px;
        box-shadow: 0 2px 27px 3px rgba(0,0,0,.06);
        padding: 30px;
        margin-bottom: 50px;
        h5{
            font-family: $primary-font;
            font-weight: 700;
            margin-bottom: 20px;
            color: $text-color-dark;
            margin-bottom: 10px;
        }
        h6{
            font-family: $primary-font;
            font-weight: 700;
            color: $text-color-dark;
            font-size: 18px;
        }
        p.stars{
            img{
                width: 20px;
                height: auto;
                margin-right: 5px;
            }
        }
        p{
            color: $text-color;
            font-size: 16px;
            line-height: 26px;
        }
        .reply{
            padding: 20px 20px 20px 35px;
            background-color: darken($color: $white, $amount: 3);
            border-left: 4px solid $primary-color;
            border-radius: 8px;
            h6{
                margin-bottom: 5px;
            }
            p{
                font-size: 14px;
                font-style: italic;
                margin-bottom: 0;
            }
        }
        .reply-form{
            margin-top: 30px;
            .form-control{
                background-color: #eeeff3;
                border: none;
                border-radius: 8px 0 0 8px;
                padding: 15px 20px;
                outline: 0;
                &:focus{
                    box-shadow: none;
                }
            }
            .btn-primary{
                padding: 15px 30px;
                background-color: $primary-color;
                border-color: $primary-color;
            }
        }
    }
}